@font-face {
  font-family: "Urbanist";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: local("Urbanist-600"), url("../fonts/urbanist-v8-latin-ext_latin-600.woff2") format("woff2"), url("../fonts/urbanist-v8-latin-ext_latin-600.woff") format("woff"); }

*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

.page {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

img {
  max-width: 100%;
  height: auto; }

.page-body {
  background-color: #000000;
  color: #ffffff;
  font-family: "Righteous";
  font-weight: 400;
  font-style: normal;
  font-size: 30px;
  line-height: 30px; }
